<template>
    <transition-group name="fade" v-if="isVisible">
        <section class="content-wrapper" key="wrapper">

            <LoadingSpinner class="center" v-if="isLoading" key="loadingSpinner"/>

            <!--v-bind:style="{'height': height === -1 ? '70%': height + '%'}"-->

            <img  v-if="!isLoading && lastData.height" ref="img" v-bind:style="{'height':  height + '%'}"
                  class="single-img" key="single-img"/>

            <div class="preview-container" key="preview" v-if="!isLoading && !!lastData.height === false">

                <div class="image-container">
                    <img ref="img" />
                </div>

                <p v-if="text.length !== 0">{{text}}</p>
            </div>

        </section>

    </transition-group>
</template>

<script>
    import {Events} from "../../common/events";
    import imageFetch from "../../common/imageFetch"
    import LoadingSpinner from "./LoadingSpinner";

    export default {
        name: "ImagePreviewContainer",

        components: {LoadingSpinner},

        data: () => {
            return {
                isVisible: false,
                isLoading: false,

                text: "",

                height: -1,
                lastData: null,
            }
        },

        methods: {
            async showImage(data) {
                this.isLoading = true;
                this.lastData = data;
                this.isVisible = true;

                this.$eventBus.$emit(Events.showUnfocusedBackground, {zIndex: 16});
                this.$eventBus.$on(Events.onBackgroundClick, () => {
                    this.hide();
                });
                window.addEventListener('scroll', this.hide);

                let res = await this.downloadImage(data.img);
                this.isLoading = false;

                if (data.height) {
                    this.height = data.height;
                }

                this.$nextTick(() => {
                    this.$refs.img.src = res;
                });

                if (data.text) {
                    this.text = await data.text();
                }
            },

            async downloadImage(src) {
                let rawRes = await imageFetch.download(src);
                return imageFetch.parse(await rawRes.arrayBuffer());
            },

            show() {
                this.isVisible = true;
            },

            hide() {
                if (!this.isVisible) return;

                if (!!this.lastData && !this.lastData.hasOwnProperty("notHideBackgroundOnClick")) {
                    this.$eventBus.$emit(Events.hideUnfocusedBackground);
                }

                this.$eventBus.$off(Events.onBackgroundClick);
                window.removeEventListener('scroll', this.hide);
                this.height = -1;
                this.isVisible = false;
                this.lastData = null;
                this.text = "";
            },
        },

        created() {
            this.$eventBus.$on(Events.showImagePreview, (data) => this.showImage(data));
        },

        destroyed() {
            this.$eventBus.$off(Events.showImagePreview);
        },
    }
</script>

<style lang="sass" scoped>

    .fade-enter-active, .fade-leave-active
        transition: 0.35s

    .fade-enter, .fade-leave-to
        opacity: 0

    .content-wrapper
        position: absolute
        z-index: 17
        top: 0
        left: 0
        right: 0
        bottom: 0
        overflow: hidden
        pointer-events: none

        .single-img
            position: fixed
            left: 50%
            top: 50%
            transform: translate(-50%, -50%)

        .preview-container
            display: flex
            justify-content: center
            position: fixed
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)
            width: 100%
            text-align: center
            //box-shadow: 0 14px 28px rgba(0, 0, 0, 0.52), 0 10px 10px rgba(0, 0, 0, 0.5)

            .image-container
                text-align: center
                align-content: baseline
                display: flex
                background-color: black

                img
                    max-width: 100%
                    width: auto
                    height: auto
                    align-self: center

            p
                max-width: 200px
                padding: 15px
                margin: 0
                overflow-y: scroll
                word-break: break-word
                font-size: 1rem
                line-height: 1.3
                color: black
                background-color: #FFFFFF
                pointer-events: bounding-box

        .center
            margin: auto
            position: fixed
            top: 0
            left: 0
            bottom: 0
            right: 0
            z-index: 17
</style>